<template lang="pug">
    #dc-table
      div.filter
        button(@click="showFilter=!showFilter") 筛选
        template(v-if="showFilter")
          .filteritem
            label(for="#filter-customer") 筛选客户：
            input#filter-customer(type="text",v-model="filterCustomer")
          .filteritem
            label(for="#start-date") 起始时间：
            input#start-date(type="date",v-model="startDate")
          .filteritem
            label(for="#end-date") 结束时间：
            input#end-date(type="date",v-model="endDate")
          .filteritem
            label(for="#min-amount") 最小金额：
            input#min-amount(type="number",v-model="minAmount")
          .filteritem
            label(for="#max-amount") 最大金额：
            input#max-amount(type="number",v-model="maxAmount")
          .filteritem
            input#paid-all(type="radio",value=0,v-model="paidFilter")
            label(for="#paid-all") 全部
            input#paid-yes(type="radio",value=1,v-model="paidFilter")
            label(for="#paid-yes") 已付款
            input#paid-no(type="radio",value=2,v-model="paidFilter")
            label(for="#paid-no") 未付款
        button(@click="showAnalyze=!showAnalyze") 统计
        .analyze(v-if="showAnalyze")
          .analyzeitem
            label(for="#analyze-start-date") 起始时间：
            input#analyze-start-date(type="date",v-model="analyzeStartDate")
          .analyzeitem
            label(for="#analyze-end-date") 结束时间：
            input#analyze-end-date(type="date",v-model="analyzeEndDate")
          .analyzeitem
            button#analyze(@click="handleAnalyze") 统计当前时间段数据
          table(v-if="analyzedInfo")
            thead
              tr
                th 客户
                th 下单数量
                th 下单金额
                th 付款数量
                th 付款金额
            tbody
              tr(v-for="(value,key) in analyzedInfo",v-if="value.orders.number || value.paids.number")
                th {{key}}
                th {{value.orders.number}}
                th {{value.orders.amount}}
                th {{value.paids.number}}
                th {{value.paids.amount}}
      table(v-if="this.orders.length")
        thead
          tr
            th 客户
            th 下单日期
            th 下单金额
            th 付款日期
            th 付款金额
            th 删除
        tbody
          template(v-for="(order,oindex) in filteredorders")
            tr.row
              td {{order.customer}}
              td {{order.orderdate}}
              td
                button(@click.prevent="currentDetail=currentDetail==oindex?-1:oindex") {{order.orderamount}}
              td(@dblclick="dateEditing=oindex")
                span(v-if="dateEditing!=oindex") {{order.paiddate}}
                input(:value="order.paiddate",type="date",v-if="dateEditing==oindex",@blur="handleUpdateDate($event.target.value,order.id)")
              td(@dblclick="amountEditing=oindex")
                span(v-if="amountEditing!=oindex") {{order.paidamount}}
                input(:value="order.paidamount",type="number",v-if="amountEditing==oindex",@blur="handleUpdateAmount(+$event.target.value,order.id)")
              td
                span.fa.fa-trash(@click="handleDeleteOrder(order.id)")
            tr.detail(v-show="currentDetail==oindex")
              td(colspan=6)
                table
                  thead
                    tr
                      th 商品
                      th 单价
                      th 数量
                      th 总价
                  tbody
                    tr(v-for="(g,gindex) in order.goods")
                      td {{g.name}}
                      td {{g.price}}
                      td {{g.number}}
                      td {{g.amount}}
</template>

<script>
import _ from "lodash";
export default {
  name: "DcTable",
  data() {
    return {
      currentDetail: -1,
      dateEditing: -1,
      amountEditing: -1,
      filterCustomer: "",
      startDate: "",
      endDate: "",
      paidFilter: 0,
      minAmount: 0,
      maxAmount: 0,
      showFilter: false,
      showAnalyze: false,
      analyzeStartDate: "",
      analyzeEndDate: "",
      analyzedInfo: null
    };
  },
  computed: {
    filteredorders() {
      let filteredorders = _.cloneDeep(this.orders);
      filteredorders = filteredorders.filter(
        function(item) {
          return new RegExp(this.filterCustomer, "i").test(item.customer);
        }.bind(this)
      );
      if (this.startDate) {
        filteredorders = filteredorders.filter(
          function(item) {
            return Date.parse(item.orderdate) >= Date.parse(this.startDate);
          }.bind(this)
        );
      }
      if (this.endDate) {
        filteredorders = filteredorders.filter(
          function(item) {
            return Date.parse(item.orderdate) <= Date.parse(this.endDate);
          }.bind(this)
        );
      }
      if (this.minAmount > 0) {
        filteredorders = filteredorders.filter(
          function(item) {
            return item.orderamount >= this.minAmount;
          }.bind(this)
        );
      }
      if (this.maxAmount > 0) {
        filteredorders = filteredorders.filter(
          function(item) {
            return item.orderamount <= this.maxAmount;
          }.bind(this)
        );
      }
      if (+this.paidFilter) {
        filteredorders = filteredorders.filter(
          function(item) {
            return +this.paidFilter == 1
              ? item.paidamount && item.paidamount > 0
              : !(item.paidamount && item.paidamount > 0);
          }.bind(this)
        );
      }
      filteredorders = filteredorders.sort((a, b) => {
        return Date.parse(b.orderdate) - Date.parse(a.orderdate);
      });
      return filteredorders;
    }
  },
  props: {
    orders: Array
  },
  methods: {
    handleDeleteOrder(id) {
      this.$emit("delete-order", id);
    },
    handleUpdateDate(paiddate, id) {
      if (paiddate) {
        this.$emit("edit-paiddate", {
          id: id,
          paiddate: paiddate
        });
      }
      this.dateEditing = -1;
    },
    handleUpdateAmount(paidamount, id) {
      if (paidamount > 0) {
        this.$emit("edit-paidamount", {
          id,
          paidamount
        });
      }
      this.amountEditing = -1;
    },
    handleAnalyze() {
      let analyzedOrders = _.cloneDeep(this.orders);
      let analyzedInfo = {};
      for (const key in analyzedOrders) {
        if (analyzedOrders.hasOwnProperty(key)) {
          const order = analyzedOrders[key];
          analyzedInfo[order.customer] = {
            orders: {
              number: 0,
              amount: 0
            },
            paids: {
              number: 0,
              amount: 0
            }
          };
        }
      }
      analyzedInfo["总计"] = {
        orders: {
          number: 0,
          amount: 0
        },
        paids: {
          number: 0,
          amount: 0
        }
      };
      analyzedOrders.forEach(order => {
        if (
          (!this.analyzeStartDate ||
            Date.parse(order.orderdate) >= Date.parse(this.analyzeStartDate)) &&
          (!this.analyzeEndDate ||
            Date.parse(order.orderdate) <= Date.parse(this.analyzeEndDate))
        ) {
          analyzedInfo[order.customer].orders.number++;
          analyzedInfo[order.customer].orders.amount += order.orderamount;
          analyzedInfo["总计"].orders.number++;
          analyzedInfo["总计"].orders.amount += order.orderamount;
        }
        if (
          order.paiddate &&
          (!this.analyzeStartDate ||
            Date.parse(order.paiddate) >= Date.parse(this.analyzeStartDate)) &&
          (!this.analyzeEndDate ||
            Date.parse(order.paiddate) <= Date.parse(this.analyzeEndDate))
        ) {
          analyzedInfo[order.customer].paids.number++;
          analyzedInfo[order.customer].paids.amount += order.paidamount;
          analyzedInfo["总计"].paids.number++;
          analyzedInfo["总计"].paids.amount += order.paidamount;
        }
      });

      this.analyzedInfo = analyzedInfo;
    }
  }
};
</script>

