<template lang="pug">
    #dc-form
      button#show-order(@click="handleShowOrder") {{showOrder?"关闭表单":"添加新订单"}}
      form#order-form(v-if="showOrder")
        label(for="#customer") 客户名称：
        input#customer(type="text",v-model.trim="form.customer",list="customers-cache")
        datalist#customers-cache
          option(v-for="(c,index) in customers",:value="c")
        label(for="#orderdate") 下单日期：
        input#orderdate(type="date",v-model="form.orderdate")
        label(for="#goods") 商品详情：
        #goods
          #goods-table
            table(v-if="this.form.goods.length")
              thead
                tr
                  th 名称
                  th 单价
                  th 数量
                  th 总价
                  th 删除
              tbody
                tr(v-for="(g,gindex) in form.goods" :key="gindex")
                  td {{g.name}}
                  td {{g.price}}
                  td {{g.number}}
                  td {{g.amount}}
                  td
                    i.delete.fa.fa-trash(@click="handleDeleteGood(gindex)")
                tr(v-if="this.form.goods.length")
                  td(colspan=2) 商品总价
                  td(colspan=3) {{form.orderamount}}
          form#goods-form
            label(for="#good-name") 商品名称：
            input#good-name(@keydown.enter="handleAddGood",type="text",v-model.trim="good.name",list="goods-cache")
            datalist#goods-cache
              option(v-for="(g,index) in goods",:value="g")
            label(for="#good-price") 商品单价：
            input#good-price(@keydown.enter="handleAddGood",type="number",v-model.number="good.price")
            label(for="#good-number") 商品数量：
            input#good-number(@keydown.enter="handleAddGood",type="number",v-model.number="good.number")
            button#add-good(@click.prevent="handleAddGood") 添加商品
          div.warning {{warning}}
        button#add-order(@click.prevent="handleAddOrder()") 添加订单
        
</template>

<script>
import _ from "lodash";
import "font-awesome/css/font-awesome.min.css";
export default {
  name: "DcForm",
  props:{
    goods:Array,
    customers:Array,
  },
  data: function() {
    return {
      showOrder:false,
      form: {
        id: 0,
        customer: "",
        orderdate: "",
        goods: [],
        orderamount: 0,
        paiddate: "",
        paidamount: 0
      },
      good: {
        name: "",
        price: 0,
        number: 0,
        amount: 0
      },
      warning: ""
    };
  },
  methods: {
    handleShowOrder(){
      this.showOrder=!this.showOrder;
    },
    handleAddGood() {
      if (!this.good.name) {
        this.warning = "请填写商品名称！";
        return;
      }
      if (+this.good.price <= 0) {
        this.warning = "商品单价必须大于0！";
        return;
      }
      if (+this.good.number <= 0) {
        this.warning = "商品数量必须大于0！";
        return;
      }
      this.warning = "";
      this.good.amount = this.good.price * this.good.number;
      this.form.goods.push(this.good);
      this.good = {
        name: "",
        price: 0,
        number: 0,
        amount: 0
      };
      this.calcAmount();
    },
    handleDeleteGood(gindex) {
      this.form.goods.splice(gindex, 1);
      this.calcAmount();
    },
    handleAddOrder() {
      if (!this.form.customer) {
        this.warning = "请输入客户名称！";
        return;
      }
      if (!this.form.orderdate) {
        this.warning = "请输入下单日期！";
        return;
      }
      if (Date.parse(this.form.orderdate)>Date.now()) {
        this.warning = "下单日期不能晚于今天！";
        return;
      }
      if (!this.form.goods.length) {
        this.warning = "商品不能为空！";
        return;
      }
      this.warning = "";
      this.form.id = Date.now();
      this.$emit("add-order", this.form);
      this.form = {
        id: 0,
        customer: "",
        orderdate: "",
        goods: [],
        orderamount: 0,
        paiddate: "",
        paidamount: 0
      };
    },
    calcAmount() {
      this.form.orderamount = _.sumBy(this.form.goods, g => g.amount);
    }
  }
};
</script>

