<template lang="pug">
  #app
    header
      h1 帝成订单管理系统
      button(@click="handleClear",hidden) 清空记录
    main
      dc-form(v-on:add-order="handleAddOrder",:goods="goods",:customers="customers")
      dc-table(:orders="orders",@delete-order="handleDeleteOrder",@edit-paiddate="handleUpdateDate",@edit-paidamount="handleUpdateAmount")
    footer(@click="goTop")
      h4 回到顶部
        span.fa.fa-arrow-up
</template>

<script>
import DcForm from "./components/DcForm";
import DcTable from "./components/DcTable";
import localforage from "localforage/dist/localforage";
import "font-awesome/css/font-awesome.min.css"
export default {
  name: "app",
  components: {
    "dc-form":DcForm,
    "dc-table":DcTable,
  },
  data:function(){
    return {
      orders:[],
      
    }
  },
  computed:{
    customers:function(){
      let customers=[];
      for (let index = 0; index < this.orders.length; index++) {
        const order = this.orders[index];
        if (!customers.includes(order.customer)) {
          customers.push(order.customer);
        }
      }
      return customers;
    },
    goods:function(){
      let goods=[];
      for (let index = 0; index < this.orders.length; index++) {
        const order = this.orders[index];
        for (let indexinner = 0; indexinner < order.goods.length; indexinner++) {
          const good = order.goods[indexinner];
          if (!goods.includes(good.name)) {
            goods.push(good.name);
          }
        }
      }
      return goods;
    }
  },
  created(){
    localforage.getItem('orders').then(function(orders){
      this.orders=orders || [];
    }.bind(this))
  },
  watch:{
    orders:{
      handler(val){
        localforage.setItem('orders',val);
      },
      deep:true,
    }
  },
  methods:{
    handleAddOrder(form){
      this.orders.push(form);
    },
    handleClear(){
      this.orders=[];
    },
    handleDeleteOrder(id){
      for (let index = 0; index < this.orders.length; index++) {
        const order = this.orders[index];
        if (order.id==id) {
          this.orders.splice(index,1);
          break;
        }
      }
    },
    handleUpdateDate({id,paiddate}){
      for (let index = 0; index < this.orders.length; index++) {
        const order = this.orders[index];
        if (order.id==id) {
          order.paiddate=paiddate;
          break;
        }
      }
    },
    handleUpdateAmount({id,paidamount}){
      for (let index = 0; index < this.orders.length; index++) {
        const order = this.orders[index];
        if (order.id==id) {
          order.paidamount=paidamount;
          break;
        }
      }
    },
    goTop(){
      scrollTo(0,0);
    }
  }
};
</script>

